  saveOtherInfo(tabIndex: number, basicform, kyc: NgForm, emp: NgForm) {
    let kycObj = this.ls.getItem('KYC_OBJ');

    const dateOfbirth = new DatePipe('en-US').transform(basicform.value.dateOfBirth, 'yyyy-MMM-dd')
    console.log(dateOfbirth);
    basicform.value.dateOfBirth = dateOfbirth;
    basicform.value.customerSubmit = false;
    var obj: any = {}
    var form_value = {
      ...basicform.value,
    }
    obj = form_value;
    obj.userAddress = this.userAddress_array.value;
    if (obj.userAddress && obj.userAddress.length == 2) {
      obj.userAddress[1].addressType = 'Permanent';
    }

    obj.createdBy = this.userName;
    obj.verificationType = "onboarding";
    obj.customerSubmit = false;

    let kycForm: any = kyc.value;
    let empForm: any = emp.value;

    var customerInfo = {

      ...kycForm,

      ...empForm,

    }
    obj.customerAdditionalInfo = customerInfo;
    obj.customerAdditionalInfo.employerName = emp.value.employerName;
    let salary = emp.value.salary;
    obj.customerAdditionalInfo.salary = salary.replaceAll(',', '');
    obj.customerAdditionalInfo.kycStatus = null;
    obj.customerId = kycObj['customerId'];

    this.apiService.insertNewUserOnboarding(obj).subscribe((result) => {
      console.log(result);
    });
    if (this.selectedIndex < 3) {
      this.selectedIndex = tabIndex + 1;
      this.ls.setItem('customerOnboadingTabIndex', this.selectedIndex);
    }
  }

  saveDataOnFirstNext(basicform) {
    let phoneNumber = basicform.value.phoneNumber;
    let payload = { phonenumber: phoneNumber, primaryemailaddress: basicform.value.primaryEmailAdress }
    console.log("this.kyc::", this.kycFetch)
    if ((!this.ls.getItem('kycReference') && !this.ls.getItem('customerId')) || (this.ls.getItem('customerId') && phoneNumber != this.ls.getItem('onboardingPhoneNumber'))) {
      this.apiService.fetchCustomerDetails(payload).subscribe((res: any) => {
        if (res != null && res.status != 204) {
          this.dialogService.phoneNumberDetailsDialogue(res).subscribe((response) => {
            if (response == "Yes") {
              this.selectedIndex = 1;
              this.ls.setItem('customerOnboadingTabIndex', this.selectedIndex);
              this.ls.setItem('onboardingPhoneNumber', basicform.value.phoneNumber)
              this.nextEnableFunction(basicform);
            }
          });
        } else {
          this.selectedIndex = 1;
          this.ls.setItem('customerOnboadingTabIndex', this.selectedIndex);
          this.ls.setItem('onboardingPhoneNumber', basicform.value.phoneNumber)
          this.nextEnableFunction(basicform);
        }
      });
    } else {
      this.selectedIndex = 1;
      this.ls.setItem('customerOnboadingTabIndex', this.selectedIndex);
      this.ls.setItem('onboardingPhoneNumber', basicform.value.phoneNumber)
      this.nextEnableFunction(basicform);
    }
  }

