  deleteSignature(report, index, action, noOfSign, changeSign) {
    console.log('report', report);

    if (action === 'delete') {
      this.dialogService.doConfirmDialog('').subscribe(resp => {
        if (resp === 'Yes') {
          console.log('yes => ', resp);
          this.confirmDelete(report, index, action, (this.mandateForm.controls.noOfSignatures.value - 1), changeSign);
        } else {
          console.log('no => ', resp);
        }
      });
    } else {
      this.confirmDelete(report, index, action, (this.mandateForm.controls.noOfSignatures.value - 1), changeSign);
    }

  }
  confirmDelete(report, index, action, noOfSign, changeSignFlag) {
    this.apiService.fetchSignatureInfo(localStorage.getItem('customerId')).subscribe((resp: any) => {
      console.log(resp);
      this.listOfSignatures = resp ? resp : [];
      this.deleteRecord = resp;


      this.apiService.getSignData().subscribe(data => {
        console.log(data);
        console.log("data.uploadModel.accountSignatureId", data.uploadModel.accountSignatureId)
        this.mandateForm.get('sign_array')['controls'][index].accountSignatureId = data.uploadModel.accountSignatureId


      })
      //this.deleteRecord = this.mandateForm.get('sign_array')['controls'] as FormArray;
      console.log('delete record', this.deleteRecord);
      console.log('index', index);


      var v: string = report.imageURL1;
      console.log("this.sign_array", this.sign_array)
      console.log(report)
      if (v.toString().includes('no-image.jpg')) {
        // this.mandateForm.controls.noOfSignatures.setValue(noOfSign)
        // this.deleteRecord.removeAt(index);
        console.log("if condition", this.mandateForm.controls.noOfSignatures.value)
        this.mandateForm.get('sign_array')['controls'].splice(index, 1);
        if (changeSignFlag === "true")
          this.mandateForm.controls.noOfSignatures.setValue(this.mandateForm.controls.noOfSignatures.value);
        else
          this.mandateForm.controls.noOfSignatures.setValue(this.mandateForm.controls.noOfSignatures.value - 1);

        this.noOfSignSelect = this.mandateForm.controls.noOfSignatures.value;
        //this.deleteRecord.removeAt(index)
        console.log('this.sign_array after :: ', this.sign_array.length, this.mandateForm.controls.noOfSignatures.value);
        if (this.sign_array.length == this.mandateForm.controls.noOfSignatures.value) {
          this.enableButton = true;
        } else {
          this.enableButton = false;
        }
        return;
      }
      if (this.deleteRecord[index].accountSignatureId != 0 && this.deleteRecord[index].accountSignatureId != null && this.deleteRecord[index].accountSignatureId != undefined &&
        this.deleteRecord[index].accountSignatureId != '') {
        this.apiService.deleteCustomerSign(this.deleteRecord[index].accountSignatureId).subscribe((data) => {
          // console.log('data => ', data);

        }, error => {

          this.apiService.fetchSignatureInfo(localStorage.getItem('customerId')).subscribe((resp: any) => {
            console.log(resp);
            this.listOfSignatures = resp ? resp : [];
            this.sign_array = resp;
            console.log('error => ', error.error.text);
            if (action === 'delete') {
              //this.deleteRecord.removeAt(index);
            }
            // this.mandateForm.controls.noOfSignatures.setValue(this.sign_array.length);
            console.log('this.sign_array after :: ', this.sign_array.length, this.mandateForm.controls.noOfSignatures.value);
            if (this.sign_array.length == this.mandateForm.controls.noOfSignatures.value) {
              if ((this.listOfSignatures.length === 0 && this.mandateForm.controls['noOfSignatures'].value === 0)) {
                this.enableButton = false;
              } else {
                this.enableButton = true;
              }
            } else if (this.sign_array.length == 0) {
              this.enableButton = false;
            }
            else {
              this.enableButton = false;
            }
          })
        }


        );
        this.mandateForm.get('sign_array')['controls'].splice(index, 1);
        this.mandateForm.controls.noOfSignatures.setValue(this.mandateForm.controls.noOfSignatures.value - 1);
        this.noOfSignSelect = this.mandateForm.controls.noOfSignatures.value;
      }// this.getListOfSignatures(localStorage.getItem('customerId'),"No")
    });

  }
