  public onUpload(event, docVal, docSideEnum, i, j, k, property, documentValFromUI) {
    console.log(i, j, k, docVal, docSideEnum, property, documentValFromUI);
    const control = this.getDocSideFormArrayControl(i, j, k, docVal);
    control.at(k).get(`${property}`).setValue(0);
    console.log(event, docVal, docSideEnum);
    console.log("docValue", docVal);
    this.selectedFiles = event.target.files[0];
    if (event.target.files) {
      let size = event.target.files[0].size;
      this.shotToast(`Your file size is ${this.formatBytes(size, 2)}`);
      if (size > 1048576) {
        this.shotToast(`Your file size is ${this.formatBytes(size, 2)}, Kindly Upload below ${this.formatBytes(1048576, 2)} file size`);
      }
      var reader = new FileReader();
      console.log(this.name);
      reader.readAsDataURL(event.target.files[0]);
      reader.onload = (event: any) => {
        this.image = event.target.result;
      }
      if (docSideEnum == 1) {
        console.log("inside if");
        this.doOCR(this.image, docVal, this.selectedFiles, docSideEnum, i, documentValFromUI, this.name, j, k);
      } else {
        this.uploadDoc(this.image, this.selectedFiles, docVal, docSideEnum, i, j, k, documentValFromUI, null, null);
      }
    }
  }

